import { Component, OnInit, ViewChild,ElementRef } from "@angular/core";
//import { Router, ActivatedRoute, Params} from '@angular/router'// para el routing
import { FiwareGEcomponents } from "../../models/fiware_ge_component"; //importar el modelo
import { fiwareGEService } from "../../services/fiwareGE.service"; // importar el servicio
import { GLOBAL } from "../../services/global";
import {GeReportService} from '../../services/ge-report.service';
import { testGEService } from "../../services/testGE.service";
import * as jsPDF from 'jspdf';
import { Images } from "../../services/logos";

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"],
  providers: [fiwareGEService, testGEService,GeReportService]
})
export class HomeComponent implements OnInit {
  public fiwareGEs: FiwareGEcomponents[];
  public status: string;
  public url: string;
  public select: string[];
  public select_image: string[];
  public porcentaje: string;
  public loading: boolean;
  public reporte: boolean;
  public reporte_user:boolean;
  public respuesta: any;
  public name_qatest: string;
  public image_qatest: string;
  public fileName: string;
  public respuestaObjJSON: any;
  public poweredByFiware: string;
  public itesmLogo: string;

  constructor(
    private _fiwareGEService: fiwareGEService,
    private _testGEService: testGEService,
    private _GeReportService: GeReportService
  ) {
    this.url = GLOBAL.url;
    this.select = [];
    this.select_image = [];
    this.porcentaje = "60%";
    this.loading = true;
    this.poweredByFiware = Images.poweredByFiware;
    this.itesmLogo = Images.itesmLogo;
  }

  ngOnInit() {
    this.getGEs();
    this.loading = false;
    this.reporte = false;
    this.reporte_user = false;
  }

  qatest(nombre,image) {
    this.name_qatest = nombre;
    this.image_qatest =  image;
    this.loading = true;
    this._testGEService.runGE(nombre).subscribe(
      Response => {
       
        this._GeReportService.getGEbyId(Response[0]._id).subscribe(Response=>{
          let res:any;
          res = Response;
          this.respuestaObjJSON = res.ge[0];
          this.fileName = this.respuestaObjJSON.ge_name+'test-'+this.respuestaObjJSON.date+'-'+this.respuestaObjJSON.time;
          this.respuesta = JSON.stringify(res.ge[0],null ,'\t');
          this.loading = false;
          this.reporte = true;
        })
       

      },
      err => {
        this.loading = false;
      }
    );
  }
  seleccion(nombre: string, url) {
    let aux;
    let posicion;

    if (this.select.length == 0) {
      aux = 0;
    } else {
      for (let i = 0; i < this.select.length; i++)
        if (this.select[i] == nombre) {
          aux = 1;
          posicion = i;
        }
    }
    if (aux == 1) {
      this.select.splice(posicion, 1);
      this.select_image.splice(posicion, 1);
    } else {
      this.select_image.push(url);
      this.select.push(nombre);
    }
  }

  exit() {
    this.reporte = false;
    this.reporte_user = false;
  }
  getGEs() {
    this._fiwareGEService.getGEs().subscribe(
      response => {
        if (!response.components) {
          this.status = "error";
        } else {
          this.fiwareGEs = response.components;
          localStorage.setItem("GE", JSON.stringify(this.fiwareGEs));
        }
      },
      error => {
        var errorMessage = <any>error;
        console.log(errorMessage);

        if (errorMessage != null) {
          this.status = "error";
        }
      }
    );
  }


  tipoReporte(){
    if(this.reporte == true){
      this.reporte = false;
      this.reporte_user = true;
    }else{
      this.reporte = true;
      this.reporte_user = false;
    }
  }
  descargar(tipo){

    if(tipo == 'adm'){
      this.downloadJSON();
      
    }else if( tipo = 'user'){
      this.downloadPDF();
    }

  }

  @ViewChild('reporte') content:ElementRef;
  downloadPDF() {
    
    let doc = new jsPDF('landscape');
        let specialELementHandlers = {
      '#editor': function (element, renderer) {
        return true;
      }
    };
    let content = this.content.nativeElement;
    doc.setFontSize(20);
    doc.text(50, 20, 'DockerQA: Docker-based Generic Enablers Quality Assurance');
    doc.addImage(this.itesmLogo, 'PNG', 30, 165, 40, 40);
    doc.addImage(this.poweredByFiware, 'PNG', 220, 180, 65, 20);
    doc.setFontSize(8);
    doc.text('CARRETERA AL LAGO DE GUADALUPE KM3.5, ATIZAPN DE ZARAGOZA C.P. 52926 \r ESTADO DE MXICO | TEL. +52 5864-5555 D.R.\rINSTITUTO TECNOLGICO Y DE ESTUDIOS SUPERIORES DE MONTERREY, MXICO. 2018', 150, 190, 'center');
    doc.fromHTML(content.innerHTML, 20, 50, {
      'width': 190,
      'elementHeadlers': specialELementHandlers
    });
    doc.save(this.fileName);
  }

  downloadJSON(){
    let files = [this.respuesta];
    let fileName = [this.fileName+'.json'];
    for(let i = 0; i < files.length ; i++){
        var file = new Blob([files[i]], {type: 'text/plain'});
        if (window.navigator.msSaveOrOpenBlob) // IE10+
            window.navigator.msSaveOrOpenBlob(file, fileName[i]);
        else { // Others
            var a = document.createElement("a"),
                    url = URL.createObjectURL(file);
            a.href = url;
            a.download = fileName[i];
            document.body.appendChild(a);
            a.click();
            setTimeout(function() {
                document.body.removeChild(a);
                window.URL.revokeObjectURL(url);  
            }, 0); 
        }
    }

}
}



