//servicio para extraer los GE components de la db
import { Injectable } from '@angular/core'; //definir los servicios y luego poder inyectarlo en cualquier otra clase
import { HttpClient, HttpHeaders } from '@angular/common/http'; //para poder hacer peticiones ajax
import {Observable} from 'rxjs/Observable'; //para recojer las respuestas que nos devuelve el api
import {GLOBAL} from './global';
import {FiwareGEcomponents} from '../models/fiware_ge_component';//importar el modelo

@Injectable()
export class fiwareGEService{
    public url: string;    

    constructor(private _http: HttpClient) {
        this.url = GLOBAL.url;
      }//para poder hacer peticiones ajax

    getGEs(): Observable<any>{
        let headers  = new HttpHeaders().set('Content-Type','application/json');
    
        return this._http.get(this.url+'ge-components',{headers:headers}); 
    }

    registerGEcomponent(ge_component: FiwareGEcomponents): Observable<any>{
        let params = JSON.stringify(ge_component);
        let headers  = new HttpHeaders().set('Content-Type','application/json');

        return this._http.post(this.url+'registerGEcomponent',params,{headers:headers});
    }
}



