// import modules of router angular
import {ModuleWithProviders} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

//import components
import {GeReportGComponent} from './components/reports/ge-report-g/ge-report-g.component';
import {GeReportComponent} from './components/reports/ge-report/ge-report.component';
import {Register_geComponent} from './components/regiter_ge_component/register_ge.component';

import {HomeComponent} from './components/home/home.component';
import {HelpComponent} from './components/help/help.component';
//import { OrionComponent } from './components/orion/orion.component';
import { componentFactoryName } from '@angular/compiler';

//array of rutes
const appRoutes: Routes = [
    {path: '', component: HomeComponent},
    {path: 'report_g', component: GeReportGComponent},
    {path: 'report_g/:page', component: GeReportGComponent},
    {path: 'geReport/:ge_name/:page',component: GeReportComponent},
    {path: 'geReport/:ge_name',component: GeReportComponent},
    {path: 'geRegister', component: Register_geComponent},
    {path: 'help', component: HelpComponent},
    {path: '**', component: HomeComponent}
];

//export module of router
export const appRoutingProviders: any[] = [];
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);


