import { Component, Input, DoCheck, OnInit, ViewChild,ElementRef } from "@angular/core";
import { testGEService } from "../../services/testGE.service";
import { GLOBAL } from "../../services/global";
import { GeReportService } from "../../services/ge-report.service";
import { Images } from "../../services/logos";
import * as jsPDF from 'jspdf';
import { Alert } from "selenium-webdriver";
@Component({
  selector: "boton2",
  templateUrl: "./boton2.component.html",
  styleUrls: ["./boton2.component.css"],
  providers: [testGEService, GeReportService]
})
export class Boton2Component implements OnInit {
  // variable que guardara los generadores que el usuario selecciono 
  public ge: any;
  //variable que nos servira de auxiliar para activar la pantalla de carga 
  public loading: boolean;
  //variable que nos servira de auxiliar para activar la pantalla donde se mostrara la respuesta del GE 
  public reporte: boolean;
  //variable que guardara la respuesta del GE mostrado en pantalla 
  public respuesta: string;
  //
  public auxiliar: number;
  //variable que guardara el nombre el GE que se mostrara en la pantalla de carga  
  public name: string;
 //variable que guardara la imagen  del GE que se mostrara en la pantalla de carga  
  public name_image;
  // variable que guardara la url con la cual accedemos al back que obtendra del servicio GLOBAL
  public url: string;
  //variable que guardara el nombre del GE que se mostrara en el reporte 
  public name_report;
  //variable donde se guardaran los nomres de todos los GE
  public nameall: string[];
  //variable donde se guardaran los ids de los GE que se correran 
  public ids_reportes: string[];
  
  public reportes: any[];
  public reporte_user: boolean;
  public NumReportes: number;
  public fileName: string;
  public respuestaObjJSON: any;
  public poweredByFiware: string;
  public itesmLogo: string;


  @Input() selecciones: string[];
  @Input() urls_image: string[];

  constructor(
    private _testGEService: testGEService,
    private _GeReportService: GeReportService
  ) {
    this.loading = true;
    this.auxiliar = 0;
    this.url = GLOBAL.url;
    this.poweredByFiware = Images.poweredByFiware;
    this.itesmLogo = Images.itesmLogo;
  }
  ngOnInit() {
    this.ids_reportes = [];
    this.loading = false;
    this.reporte = false;
    this.reportes = [];
    this.reporte_user = false;
  }

  select() {
    this.loading = true;
    if (this.selecciones.length == 0) {
      alert("Please select a GE");
      this.loading = false;
    } else if (this.auxiliar < this.selecciones.length) {
      this.RunGe(
        this.selecciones[this.auxiliar],
        this.urls_image[this.auxiliar],
        "select"
      );
    }
  }

  confirm(){
    if (confirm("Warning, this process could take several minutes!")) {
      this.all();
    }
  }

  all() {
   
      this.ge = JSON.parse(localStorage.getItem("GE"));
      this.nameall = [];
      let urls_image: string[];
      urls_image = [];
      for (let i = 0; i < this.ge.length; i++) {
        this.nameall.push(this.ge[i].ge);
        urls_image.push(this.ge[i].image);
      }
      this.loading = true;
      if (this.nameall.length == 0) {
        alert("No Datos");
        this.loading = false;
      } else if (this.auxiliar < this.nameall.length) {
        this.RunGe(this.nameall[this.auxiliar], urls_image[this.auxiliar], "all");
      }
  }

  RunGe(name, image, boton) {
    this.name = name;
    this.name_image = image;
    this._testGEService.runGE(name).subscribe(Response => {
      this.ids_reportes.push(Response[0]._id);
      if (boton == "select") {
        if (this.auxiliar + 1 < this.selecciones.length) {
          this.auxiliar = this.auxiliar + 1;
          this.select();
        } else {
          this.GEreportes(this.ids_reportes);
          this.auxiliar = 0;
        }
      } else if (boton == "all") {
        if (this.auxiliar + 1 < this.nameall.length) {
          this.auxiliar = this.auxiliar + 1;
          this.all();
        } else {
          this.GEreportes(this.ids_reportes);
          this.auxiliar = 0;
        }
      }
    });
  }

  GEreportes(ids) {
    let largo = this.ids_reportes.length;
    if (largo == 0) {
      this.loading = false;
      this.reporte = true;
      let a = this.reportes[0];
      
      this.respuestaObjJSON = a;
      this.fileName = this.respuestaObjJSON.ge_name+'test-'+this.respuestaObjJSON.date+'-'+this.respuestaObjJSON.time;
      
    
      this.NumReportes = 0;
      this.name_report = a.ge_name;
      this.respuesta = JSON.stringify(a, null, "\t");
    } else {
      this.reportesauxiliar(ids[0]);
    }
  }

  reportesauxiliar(id) {
    this._GeReportService.getGEbyId(id).subscribe(Response => {
      let aux: any = Response;
      this.reportes.push(aux.ge[0]);
      this.ids_reportes.shift();
      this.GEreportes(this.ids_reportes);
    });
  }

  tipoReporte() {
    if (this.reporte == true) {
      this.reporte = false;
      this.reporte_user = true;
    } else {
      this.reporte = true;
      this.reporte_user = false;
    }
  }

  @ViewChild('reporte') content:ElementRef;
  downloadPDF() {
    
    let doc = new jsPDF('landscape');
        let specialELementHandlers = {
      '#editor': function (element, renderer) {
        return true;
      }
    };
    let content = this.content.nativeElement;
    doc.setFontSize(20);
    doc.text(50, 20, 'DockerQA: Docker-based Generic Enablers Quality Assurance');
    doc.addImage(this.itesmLogo, 'PNG', 30, 165, 40, 40);
    doc.addImage(this.poweredByFiware, 'PNG', 220, 180, 65, 20);
    doc.setFontSize(8);
    doc.text('CARRETERA AL LAGO DE GUADALUPE KM3.5, ATIZAPN DE ZARAGOZA C.P. 52926 \r ESTADO DE MXICO | TEL. +52 5864-5555 D.R.\rINSTITUTO TECNOLGICO Y DE ESTUDIOS SUPERIORES DE MONTERREY, MXICO. 2018', 150, 190, 'center');
    doc.fromHTML(content.innerHTML, 20, 50, {
      'width': 190,
      'elementHeadlers': specialELementHandlers
    });
    doc.save(this.fileName);
  }

  downloadJSON(){
    let files = [this.respuesta];
    let fileName = [this.fileName+'.json'];
    for(let i = 0; i < files.length ; i++){
        var file = new Blob([files[i]], {type: 'text/plain'});
        if (window.navigator.msSaveOrOpenBlob) // IE10+
            window.navigator.msSaveOrOpenBlob(file, fileName[i]);
        else { // Others
            var a = document.createElement("a"),
                    url = URL.createObjectURL(file);
            a.href = url;
            a.download = fileName[i];
            document.body.appendChild(a);
            a.click();
            setTimeout(function() {
                document.body.removeChild(a);
                window.URL.revokeObjectURL(url);  
            }, 0); 
        }
    }

}

descargar(tipo){

  if(tipo == 'adm'){
    this.downloadJSON();
    
  }else if( tipo = 'user'){
    this.downloadPDF();
  }

}
  exit() {
    this.ids_reportes = [];
    this.reportes = [];
    this.respuesta = "";
    this.reporte = false;
    this.reporte_user = false;
  }

  adelante() {
    this.NumReportes = this.NumReportes + 1;
    if (this.NumReportes < this.reportes.length) {
    } else {
      this.NumReportes = 0;
    }
    let aux = this.reportes[this.NumReportes];
    this.respuestaObjJSON =  this.reportes[this.NumReportes];
    this.fileName = this.respuestaObjJSON.ge_name+'test-'+this.respuestaObjJSON.date+'-'+this.respuestaObjJSON.time;
    
    this.name_report = aux.ge_name;
    this.respuesta = JSON.stringify(
      this.reportes[this.NumReportes],
      null,
      "\t"
    );
  }
  atras() {
    this.NumReportes = this.NumReportes - 1;
    if (this.NumReportes >= 0) {
    } else {
      this.NumReportes = this.reportes.length - 1;
    }
    let aux = this.reportes[this.NumReportes];
    this.respuestaObjJSON =  this.reportes[this.NumReportes];
    this.fileName = this.respuestaObjJSON.ge_name+'test-'+this.respuestaObjJSON.date+'-'+this.respuestaObjJSON.time;
    
    this.name_report = aux.ge_name;
    this.respuesta = JSON.stringify(
      this.reportes[this.NumReportes],
      null,
      "\t"
    );
  }
}



