import { Component, OnInit } from '@angular/core';
import {Route, ActivatedRoute, Router} from '@angular/router';
import {GeReport} from '../../../gereport'; // importar el modelo
import {GeReportService} from '../../../services/ge-report.service';// Importa el servicio
import {GLOBAL} from '../../../services/global';
@Component({
  selector: 'ge-report',
  templateUrl: './ge-report.component.html',
  styleUrls: ['./ge-report.component.css'],
  providers: [GeReportService]
})
export class GeReportComponent implements OnInit {
  public ge_name;
  public page;
  public next_page;
  public prev_page;
  public total;
  public pages;
  public geReport: GeReport[];
  public status;
  public url: string;
  public ge;
  public name_image;
  public urlapi:string;
  public boton: boolean;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _gereportService: GeReportService){
      this.urlapi = GLOBAL.url;
  }

  ngOnInit() {

    this.boton = false;
    this.actualPage();
  }

  actualPage(){
    this._route.params.subscribe(params =>{
    
      
      let page = +params['page'];
      let ge_name = params['ge_name']; 
      this.page = page;
      this.ge_name = ge_name;
      this.url = "/geReport/"+ge_name;
      this.ge = ge_name.toUpperCase();
      if(!params['page']){
        page=1;
      }
      if(!page){
        page=1;
      }else{
        this.next_page = page+1;
        this.prev_page = page-1;
       
        if(this.prev_page <= 0){
          this.prev_page = 1;
        }
      }
    //Devolver reporte GE
    this.getGe(ge_name, page);
    });
  }
  getGe(ge_name, page){
    let ges = JSON.parse(localStorage.getItem('GE'));
    for(let i = 0; i< ges.length;i++){
      if(ges[i].ge == ge_name){
        this.name_image = ges[i].image
      }
    }
    console.log(this.name_image);
    
    this._gereportService.getGE(ge_name,page).subscribe(
      response =>{
        console.log(response);
        if(response.pages > 1){
          this.boton = true;
        }else{
          this.boton = false;
        }
        if(!response){
          this.status = 'error';
        }else{
          this.total = response.total;
          this.geReport = response.ge;
          this.pages = response.pages;
          if(page > this.pages){
            this._router.navigate(['/geReport/'+ge_name]);
          }
        }
      },
      error => {
        var errorMessage = <any>error;
        console.log(errorMessage);

        if(errorMessage != null){
          this.status = 'error';
        }
      }
    ); 
  }
}



