import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params} from '@angular/router'// para el routing
import {GeReport} from '../../../gereport'; //importar el modelo
import {GeReportService} from '../../../services/ge-report.service'; // importar el servicio
//import {GLOBAL} from '../../../services/global';

@Component({
  selector: 'app-ge-report-g',
  templateUrl: './ge-report-g.component.html',
  styleUrls: ['./ge-report-g.component.css'],
  providers: [GeReportService]
})
export class GeReportGComponent implements OnInit {
  public title: string;  
  public page;
  public next_page;
  public prev_page;
  public total;
  public pages;
  public geReport: GeReport[];
  public boton:boolean;
  //public geContainers: [];
  public status:string;
  public query: any;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _gereportService: GeReportService
  ){
    this.title = 'GE Report';
/*    this._gereportService.getGEreport() 
console.log(geReports);
this.geReports = geReports
        .subscribe(geReports => {
    })*/
   }

  ngOnInit() {
    this.boton= false;
    this.actualPage();
  }
  actualPage(){
    this._route.params.subscribe(params =>{
      let page = +params['page'];
      this.page = page;
      if(!params['page']){
        page=1;
      }
      if(!page){
        page = 1;
      }else{
        this.next_page = page+1;
        this.prev_page = page-1;

        if(this.prev_page <= 0){
          this.prev_page = 1;
        } 
      }
    // devolver listado de GE's
    this.getGes(page)
    });
  }
  getGes(page){
    this._gereportService.getGEreport(page).subscribe(
      response => {
        console.log(response);
        if(response.pages > 1){
          this.boton = true;
        }else{
          this.boton = false;
        }
        if(!response.ges){
          this.status = 'error';
        }else{
          //console.log(response);   
          this.total = response.total;
          this.geReport = response.ges;
          this.pages = response.pages; 
          if(page > this.pages){
            this._router.navigate(['/report_g',1]);
          }
        }
      },
      error => {
        var errorMessage = <any>error;
        console.log(errorMessage);

        if(errorMessage != null){
          this.status ='error';
        }
      }
    );
  }
}


