import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
//import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
import {routing, appRoutingProviders} from './app.routing'

import {GeSearchPipe} from './pipes/geFilter.pipe';

//import { VirtualAction } from 'rxjs/scheduler/VirtualTimeScheduler'; //scroll menu

import { AppComponent } from './app.component';
import { MenuComponent } from './components/menu/menu.component';
import { Boton2Component } from './components/boton2/boton2.component';
import { HomeComponent } from './components/home/home.component';
import { HelpComponent } from './components/help/help.component';
import { BarraComponent } from './components/barra/barra.component';

import { GeReportService } from'./services/ge-report.service'; //import GeReport service 
import { GeReportComponent } from './components/reports/ge-report/ge-report.component';
import { GeReportGComponent } from './components/reports/ge-report-g/ge-report-g.component';
import {Register_geComponent} from './components/regiter_ge_component/register_ge.component';
import { FooterComponent } from './components/footer/footer.component';


@NgModule({
  declarations: [
    AppComponent,
    MenuComponent,
    Boton2Component,
    GeReportComponent,
    HomeComponent,
    GeReportGComponent,
    HelpComponent,
    BarraComponent,
    GeSearchPipe,
    Register_geComponent,
    FooterComponent
//    VirtualAction,
  ], 
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    routing,
  ],
  providers: [
    GeReportService,
    appRoutingProviders 
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



