import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, Params } from "@angular/router";
import { FiwareGEcomponents } from "../../models/fiware_ge_component"; //importar el modelo
import { fiwareGEService } from "../../services/fiwareGE.service";
import { UploadService } from "../../services/upload.service";
import { GLOBAL } from "../../services/global";
import { FILE } from "dns";

@Component({
  selector: "register_ge_component",
  templateUrl: "./register_ge.component.html",
  styleUrls: ["./register_ge.componet.css"],
  providers: [fiwareGEService, UploadService]
})
export class Register_geComponent implements OnInit {
  public title: string;
  public ge_component: FiwareGEcomponents;
  public url: string;
  public filesToUpload: Array<File>;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _geComponentService: fiwareGEService,
    private _makeFile: UploadService
  ) {
    this.url = GLOBAL.url;
    this.title = "Registra el GE";
    this.ge_component = new FiwareGEcomponents("", "", "", "");
  }

  ngOnInit() {
    console.log("compoente de registro cargado...");
  }

  fileChangeEvent(fileInput: any) {
    this.filesToUpload = <Array<File>>fileInput.target.files;
  }
  onSubmit() {
    this._geComponentService.registerGEcomponent(this.ge_component).subscribe(
      response => {
        if (response.ge_comp._id) {
          let t: any;
          t = response;

          this._makeFile
            .makeFileRequest(
              this.url + "upload-image-gecomp/" + response.ge_comp.ge,
              [],
              this.filesToUpload,
              "image"
            )
            .then((result: any) => {
              if (result.geUpdate._id) {
                alert("Imagen Guardada");
              } else {
                alert("Error");
              }
            });
        } else {
          alert("error");
        }
      },
      error => {
          alert('Puede que el componente ya exista')
        console.log(<any>error);
      }
    );
  }
}



