import { Component, OnInit } from "@angular/core";

import { FiwareGEcomponents } from "../../models/fiware_ge_component";
import { fiwareGEService } from "../../services/fiwareGE.service";
import { GLOBAL } from "../../services/global";

@Component({
  selector: "menu",
  templateUrl: "./menu.component.html",
  styleUrls: ["./menu.component.css"],
  providers: [fiwareGEService]
})
export class MenuComponent implements OnInit {
  public fiwareGEs: FiwareGEcomponents[];
  public status: string;
  public url: string;
  //title = 'app works!';

  constructor(private _fiwareGEService: fiwareGEService) {
    this.url = GLOBAL.url;
  }

  ngOnInit() {
    this.getGEs();
  }

  getGEs() {
    this._fiwareGEService.getGEs().subscribe(
      response => {
        if (!response.components) {
          this.status = "error";
        } else {
          this.fiwareGEs = response.components;
        }
      },
      error => {
        var errorMessage = <any>error;
        console.log(errorMessage);

        if (errorMessage != null) {
          this.status = "error";
        }
      }
    );
  }
}



