import { Injectable } from '@angular/core'; //definir los servicios y luego poder inyectarlo en cualquier otra clase
import { HttpClient, HttpHeaders } from '@angular/common/http'; //para poder hacer peticiones ajax
import {Observable} from 'rxjs/Observable'; //para recojer las respuestas que nos devuelve el api
import {GLOBAL} from './global';
import {GeReport} from '../gereport';//importar el modelo

@Injectable()
export class GeReportService {
  public url:string;

  constructor(private _http: HttpClient) {
    this.url = GLOBAL.url;
  }//para poder hacer peticiones ajax

  getGEreport(page = null): Observable<any>{
    let headers  = new HttpHeaders().set('Content-Type','application/json');
    
    return this._http.get(this.url+'GES/'+page,{headers:headers}); 
  }

  getGE(ge_name, page): Observable<any>{
    let headers  = new HttpHeaders().set('Content-Type','application/json');
    
    return this._http.get(this.url+'GE/'+ge_name+'/'+page,{headers:headers}); 
  }

  getGErep(){
    return this._http.get<GeReport[]>(this.url)
    //.map(res => res);
  }
  getGEbyId(id){
    let headers  = new HttpHeaders().set('Content-Type','application/json');
    
    return this._http.get(this.url+'getGEbyId/'+id,{headers:headers}); 
  }
}



